    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    --fw-bold: 700;
    --fw-normal: 300;
    --fw-tinny: 200;
    --fs-100: 0.3rem;
    --fs-200: 0.7rem;
    --fs-300: 1rem;
    --fs-700: 2.5rem;
    --clr-neutral-100: #fff;
    --clr-neutral-300: #e6e6e6;
    --clr-neutral-500: #6d6c6c;
    --clr-neutral-700: #000000;
    --clr-primary-700: ;
    --clr-primary-500: ;
    --clr-primary-200: ;
    --clr-primary-100: ;
}

body {
    min-height: 100vh;
    width: 100vw;
    display: grid;
    grid-template-columns: 2fr 8fr;
}

svg {
    height: 2rem;
    width: auto;
}

.aside {
    display: flex;
    flex-flow: nowrap column;
    grid-area: 1 / 1 / -1 / 2;
}

.profile {
    padding: 1rem;
    display: flex;
    gap: 1rem;
    align-items: center;
}

.profile-pic {
    width: 3rem;
    height: 3rem;
    border-radius: 100%;
    background-color: var(--clr-neutral-400, gray);
}

.profile-name {
    font-size: var(--fs-300);
}

.search {
    display: flex;
    justify-content: center;
    align-items: center;
}

#search-form {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    position: relative;
}

#search-query {
    height: 4rem;
    padding-left: 1rem;
}

#search-query:focus {
    outline: none;
}

#cancel-search-query {
    position: absolute;
    left: 85%;
    cursor: pointer;
    font-weight: var(--fw-bold);
}

.main-nav, .category-nav {
    display: flex;
    flex-flow: column;
    gap: 1rem;
}

.main-nav  button, .category-nav  button {
    text-align: start;
    border: none;
    background-color: var(--clr-neutral-100);
    padding: 10px 0;
    transition: transform 1s ease;
}

.transform:hover {
    transform: scale(1.1);
    box-shadow: 0 8px 12px rgba(0, 0, 0, 0.4); /* Aumenta la sombra */
}

.main-nav p {
    width: 95%;
    display: flex;
    gap: 1rem;
    justify-content: start;
    align-items: center;
    padding: 0 1.5rem;
    transition: 1s ease transform;
}

.main {
    display: flex;
    flex-flow: column nowrap;
    padding: 3rem;
    box-shadow: 1px 1px 1rem;
    gap: 1rem;
}

.main-sections {
    display: flex;
    padding-bottom: 1rem;
    gap: 3rem;
    border-bottom: solid var(--clr-neutral-500) 1px;
}

.main-sections button {
    padding: 1rem 5rem;
    flex: 1;
    border: none;
    background-color: var(--clr-neutral-100);
}

.main-sections button.clicked {
    background-color: var(--clr-neutral-300);
}

.main-todo-board {
    flex: 1;
}

.task-item {
    display: flex;
    gap: 1rem;
    transition: transform 1s ease;
    padding-left: 1rem;
}

.task-item:hover {
    transform: scale(1.05);
    box-shadow: 1px 1px 0.5rem;
}

.todo-checkbox {
    width: 1rem;
    height: auto;
}

.todo-item-todo {
    cursor: pointer;
}

.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color:  rgba(0, 0, 0, 0.5);

    display: flex;
    align-items: center;
    justify-content: center;
}

.modal {
    width: 30%;
    height: 50%;
    opacity: 1;
    background-color: var(--clr-neutral-100);
    padding: 1.5rem;
    display: flex;
    flex-flow: column;
    gap: 1rem;
    border-radius: var(--fs-100);
    
}

.close-details-btn {
    width: 2rem;
    height: auto;
    border: none;
    background-color: var(--clr-neutral-100);
    font-weight: var(--fw-bold);
    transition: transform ease 1s;
}

.close-details-btn:hover {
    transform: scale(1.3);
    color: var(--clr-neutral-500);
}

.remove-todo-btn {
    border: none;
    background-color: #b91c1c;
    color: var(--clr-neutral-100);
    padding: var(--fs-100) 0;
    border-radius: var(--fs-100);
}

.todo-inputs {
    display: flex;
    flex-flow: column wrap;
    gap: 1rem;
}

.todo-inputs p {
    display: flex;
}

.todo-inputs p label {
    flex: 1;
    font-weight: var(--fw-bold);
    font-size: var(--fs-300);
}

.todo-inputs p input,select {
    flex: 3;
    border-radius: var(--fs-100);
    padding: var(--fs-100);
}

#todo-task, #todo-category {
    border: none;
    border-bottom: 2px solid var(--clr-neutral-300);
}

.todo-inputs p input:focus,select:focus, textarea:focus{
    outline: none;
}

textarea {
    resize: none;
    width: 100%;
    height: 70px;
    border-radius: var(--fs-100);
    border: none;
    border-bottom: 2px solid var(--clr-neutral-300);
}

#submitform {
    border: none;
    background-color:#0c4a6e;
    font-weight: var(--fw-bold);
    color: var(--clr-neutral-100);
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,mJAAmJ;IACnJ,cAAc;IACd,gBAAgB;IAChB,eAAe;IACf,gBAAgB;IAChB,gBAAgB;IAChB,cAAc;IACd,gBAAgB;IAChB,uBAAuB;IACvB,0BAA0B;IAC1B,0BAA0B;IAC1B,0BAA0B;IAC1B,mBAAmB;IACnB,mBAAmB;IACnB,mBAAmB;IACnB,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,YAAY;IACZ,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,aAAa;IACb,wBAAwB;IACxB,yBAAyB;AAC7B;;AAEA;IACI,aAAa;IACb,aAAa;IACb,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,mBAAmB;IACnB,8CAA8C;AAClD;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,eAAe;IACf,2BAA2B;AAC/B;;AAEA;IACI,aAAa;IACb,iBAAiB;IACjB,SAAS;AACb;;AAEA;IACI,iBAAiB;IACjB,YAAY;IACZ,wCAAwC;IACxC,eAAe;IACf,6BAA6B;AACjC;;AAEA;IACI,qBAAqB;IACrB,yCAAyC,EAAE,sBAAsB;AACrE;;AAEA;IACI,UAAU;IACV,aAAa;IACb,SAAS;IACT,sBAAsB;IACtB,mBAAmB;IACnB,iBAAiB;IACjB,6BAA6B;AACjC;;AAEA;IACI,aAAa;IACb,wBAAwB;IACxB,aAAa;IACb,wBAAwB;IACxB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,oBAAoB;IACpB,SAAS;IACT,+CAA+C;AACnD;;AAEA;IACI,kBAAkB;IAClB,OAAO;IACP,YAAY;IACZ,wCAAwC;AAC5C;;AAEA;IACI,wCAAwC;AAC5C;;AAEA;IACI,OAAO;AACX;;AAEA;IACI,aAAa;IACb,SAAS;IACT,6BAA6B;IAC7B,kBAAkB;AACtB;;AAEA;IACI,sBAAsB;IACtB,0BAA0B;AAC9B;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,eAAe;IACf,WAAW;IACX,YAAY;IACZ,qCAAqC;;IAErC,aAAa;IACb,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,UAAU;IACV,WAAW;IACX,UAAU;IACV,wCAAwC;IACxC,eAAe;IACf,aAAa;IACb,iBAAiB;IACjB,SAAS;IACT,4BAA4B;;AAEhC;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,YAAY;IACZ,wCAAwC;IACxC,2BAA2B;IAC3B,6BAA6B;AACjC;;AAEA;IACI,qBAAqB;IACrB,6BAA6B;AACjC;;AAEA;IACI,YAAY;IACZ,yBAAyB;IACzB,6BAA6B;IAC7B,wBAAwB;IACxB,4BAA4B;AAChC;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,OAAO;IACP,2BAA2B;IAC3B,wBAAwB;AAC5B;;AAEA;IACI,OAAO;IACP,4BAA4B;IAC5B,sBAAsB;AAC1B;;AAEA;IACI,YAAY;IACZ,+CAA+